html[data-theme="dark"] {
	--color--bg-gradient-0: var(--color--bg-0);

	--color--0: rgb(0 0 0);
	--color--1: rgb(255 255 255);

	--color--a1-0: rgb(127 188 103);
	--color--a1-1: rgb(143 201 120);
	--color--a1-2: rgb(160 213 139);

	--color--a2-0: rgb(84 169 192);
	--color--a2-1: rgb(101 182 203);
	--color--a2-2: rgb(121 196 215);

	--color--a3-0: rgb(139 139 198);
	--color--a3-1: rgb(156 156 211);
	--color--a3-2: rgb(180 180 218);

	--color--a4-0: rgb(198 139 186);
	--color--a4-1: rgb(211 156 200);
	--color--a4-2: rgb(222 175 213);

	--color--a5-0: rgb(219 117 117);
	--color--a5-1: rgb(229 140 140);
	--color--a5-2: rgb(236 161 161);

	--color--el-0: rgb(70 70 70);
	--color--el-1: rgb(90 90 90);
	--color--el-2: rgb(110 110 110);

	--color--f1-0: rgb(180 180 180);
	--color--f1-1: rgb(200 200 200);
	--color--f1-2: rgb(220 220 220);

	--color--f2-0: rgb(84 84 84);
	--color--f2-1: rgb(100 100 100);
	--color--f2-2: rgb(115 115 115);

	--color--bg-0: rgb(20 20 20);
	--color--bg-1: rgb(30 30 30);
	--color--bg-2: rgb(40 40 40);

	--color--scroll-thumb: rgb(45 45 45);

	// No changing
	--color--0-static: rgb(0 0 0);
	--color--1-static: rgb(255 255 255);

	// ...
}
